body {
  padding: 0;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
  color: rgb(211, 210, 210);
  box-sizing: border-box;
  margin: 0;
  height: 100vh;
  font-size: 12px;
  overflow: hidden;
}

.light {
  background: white;
}

.roomContainer {
  height: 100vh;
}

.error {
  color: red;
}

.lk-grid-layout-wrapper {
  height: 100%;
}

.lk-focus-layout {
  height: 100%;
}

/* things like name, connection quality, etc make less sense in a recording, hide for now */
.lk-participant-metadata {
  display: none;
}


/* ============================================================== */
.rosterLayoutWrapper {
  width: 100%;
  height: 100vh;
  background: #19191a;
}

.rosterLayout {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 12px;
  padding: 16px;
  /* background: #19191a; */
  /* width: 100%; */

}

.participant_identity {
  background: #333;
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  color: #fff;
  border: 4px solid transparent;
  position: relative;
}

.participant_wrap {
  width: 100%;
  height: 180px;
  background: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  border-radius: 8px;

}

.participant_name {
  position: absolute;
  bottom: 8px;
  left: 8px;
  /* margin-top: 8px; */
  font-size: 16px;
  background: #0000004a;
  border-radius: 7px;
  padding: 3px 10px;
}

.participant_wrap svg {
  width: 143px;
  fill: #595959;
}

.speaking {
  border: 4px solid #00FF7F;
  transition: border 0.2s ease-in-out;
}